@mixin onDesktop {
  @media (min-width: 1020px) {
    @content;
  }
}

@mixin onTablet {
  @media (min-width: 600px) {
    @content;
  }
}

@mixin top-actions {
  display: grid;
  grid-template-columns: 1fr $icon-width $icon-width;
  align-items: center;
  gap: 30px;

  @include onTablet {
    grid-template-columns: 1fr $icon-width-desktop $icon-width-desktop;
  }
}
